@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.landing {
  font-size: 6em;
  margin: .5em 1em 0 1em;
}

.skills {
  margin: 1em 5em;
}

/* #main {
  background-color: pink;
} */
#work {
    padding-bottom: 2em;
    padding-top: 6em;
    margin-top: 5em;
}

.workCard {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-bottom: 2em;
}

.workText {
    display: flex;
    flex-direction: column;
    padding: .25em;
}
#about {
    padding-top: 6em;
}

/* #aboutPage {
    background-color: pink;
} */
#connect {
  padding-top: 6em;
  padding-bottom: 22em;
}

.icons {  
  margin: .5em 4em 0 4em;
  color: black;
};

.iconList {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.art {
  width: 100%;
  height: 100%;
  /* position: absolute; */
  top: 0;
  left: 0;
}

header {
  color: black;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  font-family: 'Bebas Neue', cursive;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  z-index: 5;
  background-color: white;
}

.stuff {
  margin-top: 103px;
  overflow-y: scroll;
}

header a:link {
  text-decoration: none;
  color: black;
}

header a:hover {
  text-decoration: underline;
}

header a:active {
  text-decoration: underline;
}

header a:visited {
  text-decoration: none;
  color: black;
}

header h1{
  padding: .5em;
  margin-top: 0;
  text-align: left;
}

nav {
  padding: .5em;
  margin-top: 0;
  text-align: right;
  font-size: 2em;
}

nav ul {
  display: flex;
  margin-top: 0;
  list-style-type: none;
}

li {
  padding: 0 .25em;
}

li a:link {
  text-decoration: none;
  color: black;
}

li a:hover {
  text-decoration: underline;
}

li a:active {
  text-decoration: underline;
}

li a:visited {
  text-decoration: none;
  color: black;
}

