@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

.art {
  width: 100%;
  height: 100%;
  /* position: absolute; */
  top: 0;
  left: 0;
}

header {
  color: black;
  position: sticky;
  top: 0;
  font-family: 'Bebas Neue', cursive;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  z-index: 5;
  background-color: white;
}

.stuff {
  margin-top: 103px;
  overflow-y: scroll;
}

header a:link {
  text-decoration: none;
  color: black;
}

header a:hover {
  text-decoration: underline;
}

header a:active {
  text-decoration: underline;
}

header a:visited {
  text-decoration: none;
  color: black;
}

header h1{
  padding: .5em;
  margin-top: 0;
  text-align: left;
}

nav {
  padding: .5em;
  margin-top: 0;
  text-align: right;
  font-size: 2em;
}

nav ul {
  display: flex;
  margin-top: 0;
  list-style-type: none;
}

li {
  padding: 0 .25em;
}

li a:link {
  text-decoration: none;
  color: black;
}

li a:hover {
  text-decoration: underline;
}

li a:active {
  text-decoration: underline;
}

li a:visited {
  text-decoration: none;
  color: black;
}
